<template>
    <vs-chip class="ag-grid-cell-chip" :color="chipColor(params.value)">
      <span>{{ $t(textMethod(params.value)) }}</span>
    </vs-chip>
</template>

<script>
    export default {
        name: 'CellRendererPaymentMethod',
        computed: {
          chipColor() {
            return (value) => {
              switch (value) {
                case 1:
                  return 'primary'
                case 2:
                  return 'primary'
                default:
                  return value
              }
            }
          },
          textMethod() {
            return (value) => {
              switch (value) {
                case 1:
                  return 'order.payment.method.slip'
                case 2:
                  return 'order.payment.method.credit'
                default:
                  return value
              }
            }
          }
        }
    }
</script>

<style lang="scss" scpoped>
.ag-grid-cell-chip {
  &.vs-chip-success {
    background: rgba(var(--vs-success),.15);
    color: rgba(var(--vs-success),1) !important;
    font-weight: 500;
  }
  &.vs-chip-warning {
    background: rgba(var(--vs-warning),.15);
    color: rgba(var(--vs-warning),1) !important;
    font-weight: 500;
  }
  &.vs-chip-danger {
    background: rgba(var(--vs-danger),.15);
    color: rgba(var(--vs-danger),1) !important;
    font-weight: 500;
  }
}
</style>
